.page {
  scroll-behavior: smooth;
  color: #f1f5f4;
  box-sizing: border-box;
  min-width: 320px;
  margin: 0;
  padding: 0;
  transition: all .3ms;
}

.page:has(.menu:target) {
  overflow: hidden;
}

.button-type1, .button-type2 {
  text-align: center;
  padding: unset;
  cursor: pointer;
  outline: none;
  height: 60px;
}

.button-type1 {
  color: #f1f5f4;
  background-color: #cd4d31;
  border: none;
}

.button-type1:hover {
  background-color: #af3419;
}

.button-type1:active {
  background-color: #ea340d;
}

.button-type1:disabled {
  background-color: #dd836f;
}

.button-type2 {
  color: #cd4d31;
  background-color: #f1f5f4;
  border: 1px solid #cd4d31;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  display: flex;
}

.button-type2:hover {
  color: #af3419;
  border: 1px solid #af3419;
}

.button-type2:active {
  color: #ea340d;
  border: 1px solid #ea340d;
}

.button-type2:disabled {
  color: #dd836f;
  border: 1px solid #dd836f;
}

.section-title {
  color: #1c1b29;
